import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';
@IonicPage()
@Component({
  selector: 'page-event-create',
  templateUrl: 'event-create.html',
})
export class EventCreatePage {
  constructor(public navCtrl: NavController, public eventProvider:
    EventProvider) { }

  createEvent(eventName: string, eventEspecificacoes: string, eventPrice: number, eventEndereco: string, eventLargura:number, eventComprimento:number, eventDate: string, eventDate1: string, eventDate2: number): void {
    this.eventProvider.createEvent(eventName, eventEspecificacoes, eventPrice, eventEndereco, eventLargura, eventComprimento, eventDate, eventDate1, eventDate2)
      .then(newEvent => {
        this.navCtrl.pop();
      })
  }
}


